.App {
  text-align: center;
  margin-top: 50px;
}

button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.fade-message {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3em;
  font-weight: bold;
  z-index: 10000;
  background-color: #ffffcc;
  padding: 10px;
}

.fade-message.show {
  opacity: 1;
}

.correct {
  color: green;
  font-weight: bold;
}

.incorrect {
  color: red;
  font-weight: bold;
}

.num {
  font-weight: bold;
}

.dude {
  max-height: 612px;
  max-width: 612px;
}